import Motion from './Motion.vue'
import presets from './presets'

export default {
  data () {
    return {
      n: 0,
      max: 400,
      config: {
        stiffness: 170,
        damping: 26,
        precision: 0.01,
      },
    }
  },

  computed: {
    presets () {
      return presets
    }
  },

  methods: {
    toggle () {
      this.n = this.n < this.max / 2
             ? this.max
             : 0
    },
    setSpring (config) {
      this.config = config
      this.config.precision = this.config.precision || 0.01
    },
    start () {
      console.log('---------')
      console.log('Start')
      console.time('motion')
    },
    end () {
      console.log('Stop')
      console.timeEnd('motion')
      console.log('---------')
    },
    restart () {
      console.log('Restart')
    },
  },

  components: { Motion }
}



